package mzx.ventas.domain.usecase

import mzx.ventas.data.LocalReaderJson
import mzx.ventas.products.ProductItem

interface GetProductsUseCase {
    suspend fun action(productFilter: ProductFilter= ProductFilter.ALL):List<ProductItem>
}

class GetProductsUseCaseImpl constructor(val localReaderJson: LocalReaderJson): GetProductsUseCase {
    override suspend fun action(productFilter: ProductFilter): List<ProductItem> {
        return localReaderJson.load("data")
    }
}

enum class ProductFilter {
    ALL
}

