package mzx.ventas.products

import kotlinx.coroutines.CoroutineScope
import kotlinx.coroutines.MainScope
import kotlinx.coroutines.cancel
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.asStateFlow
import kotlinx.coroutines.flow.update
import kotlinx.coroutines.launch
import mzx.ventas.domain.usecase.GetProductsUseCase
import mzx.ventas.products.ProductsUiState.Companion.EMPTY

class ProductsViewModel constructor(private val getProductsUseCase: GetProductsUseCase) : CoroutineScope by MainScope() {

    private val _state: MutableStateFlow<ProductsUiState> = MutableStateFlow(
        EMPTY
    )
    val state = _state.asStateFlow()

    fun onEvent(event: ProductsUiEvent) {
        when(event){
            ProductsUiEvent.Init -> launch {
                val result: List<ProductItem> = getProductsUseCase.action()
                _state.update {
                    it.copy(items = result)
                }
            }
        }
    }

    fun onDispose() {
        cancel() // cancels all coroutines
    }
}


data class ProductsUiState(val items: List<ProductItem>) {
    companion object {
        val EMPTY = ProductsUiState(emptyList())
    }
}

sealed interface ProductsUiEvent {
    object Init : ProductsUiEvent
}
