package mzx.ventas.data

import com.varabyte.kobweb.core.AppGlobals
import kotlinx.browser.window
import kotlinx.coroutines.await
import kotlinx.serialization.json.Json
import mzx.ventas.products.ProductItem


interface LocalReaderJson {
    suspend fun load(fileName: String): List<ProductItem>
}

class LocalReaderJsonImp : LocalReaderJson {
    override suspend fun load(fileName: String): List<ProductItem> {
        val basePath = AppGlobals.basePath()

        val response = window.fetch("/$basePath/data/$fileName.json").await()
        val text = response.text().await()
        val obj = Json.decodeFromString<List<ProductItem>>(text)

        console.log(obj)

        return obj
    }
}

private fun AppGlobals.basePath(): String = try {
    getValue("basePath")
} catch (e: Exception) {
    "saboresapp.github.io"
}
